module.exports = function (url) {
  var typeOfUrl = typeof url;

  if (typeOfUrl !== 'string') {
    throw new TypeError("\n      URL type is invalid. Expected a string, but got: ".concat(typeOfUrl, "\n    "));
  }

  var protocol = '(?:(?:https?)://)';
  var ipv4 = '(?:[1-9]\\d?|1\\d\\d|2[01]\\d|22[0-3])(?:\\.(?:1?\\d{1,2}|2[0-4]\\d|25[0-5])){2}(?:\\.(?:[1-9]\\d?|1\\d\\d|2[0-4]\\d|25[0-4]))';
  var hostname = "(?:(?:[a-z\\u00a1-\\uffff0-9]-*)*[a-z\\u00a1-\\uffff0-9]+)";
  var domain = "(?:\\.(?:[a-z\\u00a1-\\uffff0-9]-*)*[a-z\\u00a1-\\uffff0-9]+)*";
  var tld = "(?:\\.(?:[a-z\\u00a1-\\uffff]{2,}))";
  var port = '(?::\\d{2,5})?';
  var resourcePath = '(?:[/?#]\\S*)?';
  var regex = "".concat(protocol, "(?:localhost|").concat(ipv4, "|").concat(hostname).concat(domain).concat(tld, "\\.?)").concat(port).concat(resourcePath);
  var isUrl = new RegExp("^".concat(regex, "$"), 'i');
  return isUrl.test(url);
};